export default {
	data() {
		return {
			gridCol: 3,
			gridBorder: '',
			modalName: '',
			iconList: [{
				icon: 'cardboardfill',
				color: 'red',
				badge: 120,
				name: 'VR'
			}, {
				icon: 'recordfill',
				color: 'orange',
				badge: 1,
				name: '录像'
			}, {
				icon: 'picfill',
				color: 'yellow',
				badge: 0,
				name: '图像'
			}, {
				icon: 'noticefill',
				color: 'olive',
				badge: 22,
				name: '通知'
			}, {
				icon: 'upstagefill',
				color: 'cyan',
				badge: 0,
				name: '排行榜'
			}, {
				icon: 'clothesfill',
				color: 'blue',
				badge: 0,
				name: '皮肤'
			}, {
				icon: 'discoverfill',
				color: 'purple',
				badge: 0,
				name: '发现'
			}, {
				icon: 'questionfill',
				color: 'mauve',
				badge: 0,
				name: '帮助'
			}, {
				icon: 'commandfill',
				color: 'purple',
				badge: 0,
				name: '问答'
			}, {
				icon: 'brandfill',
				color: 'mauve',
				badge: 0,
				name: '版权'
			}],
			menuBorder: '',
			menuCard: '',
			menuArrow: '',
			listTouchStart: 0,
			listTouchDirection: ''
		}
	},
	methods: {
		showModal(e) {
			let that = this
			that.modalName = e.currentTarget.dataset.target
		},
		hideModal() {
			let that = this
			that.modalName = ''
		},
		gridchange(e) {
			let that = this
			that.gridCol = e.detail.value
		},
		gridswitch(e) {
			let that = this
			that.gridBorder = e.detail.value
		},
		menuBorderFn(e) {
			let that = this
			that.menuBorder = e.detail.value
		},
		menuArrowFn(e) {
			let that = this
			that.menuArrow = e.detail.value
		},
		menuCardFn(e) {
			let that = this
			that.menuCard = e.detail.value
		},
		// 列表左滑
		listTouchStartFn(e) {
			let that = this
			that.listTouchStart = e.touches[0].pageX
		},
		listTouchMoveFn(e) {
			let that = this
			that.listTouchDirection = e.touches[0].pageX - that.listTouchStart > 0 ? 'right' : 'left'
		},
		listTouchEndFn(e) {
			let that = this
			if (that.listTouchDirection == 'left') {
				that.modalName = e.currentTarget.dataset.target
			} else {
				that.modalName = ''
			}
			that.listTouchDirection = ''
		}
	}
}
